* {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }
body {
    background: #E5E5E5;
    font-family: 'Montserrat', sans-serif; }
header {
    display: flex;
    width: 1920px;
    height: 91px;
    background: #EBEEF5; }
a {
    text-decoration: none; }
.header-container {
    display: flex;
    margin-left: 390px;
    .logo-container {
        display: flex;
        flex-direction: column;
        margin-top: 20px;
        .img-container {
            display: flex;
            align-items: center;
            img:first-of-type {
                margin-right: 5.89px; } }
        .first {
            position: relative;
            width: 149.1px;
            height: 15.38px;
            margin-top: 3.85px;
            background: #2D3786;
            border-radius: 3.92357px;
            .second {
                display: flex;
                position: absolute;
                width: 147.13px;
                height: 15.38px;
                left: 4.9px;
                top: 3.85px;
                background: #397DFF;
                border-radius: 3.92357px;
                p {
                    font-size: 9.80892px;
                    color: #FFFFFF;
                    margin: 0 auto;
                    align-self: center; } } } }
    .menu-container {
        display: flex;
        align-items: flex-end;
        margin-left: 29.96px;
        a {
            color: rgba(43, 45, 58, 0.7);
            font-size: 14px;
            margin-left: 33px;
            &:first-of-type {
                margin-left: 0; }
            &:hover {
                color: #397DFF; }
            &:hover .border {
                visibility: visible; }
            .border {
                margin: 0 auto;
                width: 48px;
                border-top: 3px solid #397DFF;
                margin-top: 34px;
                visibility: hidden; } } }
    .email-container {
        display: flex;
        align-items: center;
        margin-left: 27px;
        a {
            img {
                margin-top: 5px; } }
        a:nth-of-type(2) {
            text-decoration: none;
            margin-left: 10px;
            font-size: 13px;
            color: #000;
            font-weight: 600; } }
    .phone-container {
        display: flex;
        align-items: center;
        margin-left: 24px;
        .tel-container {
            display: flex;
            flex-direction: column;
            margin-top: 10px;
            margin-left: 10px;
            a {
                color: #397DFF;
                font-size: 11px; }
            p {
                margin-top: 0;
                font-size: 13px;
                font-weight: 600; } } } }
section.slider {
    .main-image {
        position: relative;
        .mainText-container {
            position: absolute;
            top: 123px;
            left: 390px;
            p {
                color: white;
                margin: 0;
                &:first-of-type {
                    font-size: 18px;
                    line-height: 22px; }
                &:nth-of-type(2) {
                    font-size: 52px;
                    line-height: 63px;
                    margin-top: 12px;
                    margin-left: -5px; }
                &:nth-of-type(3) {
                    font-size: 16px;
                    line-height: 148%;
                    margin-top: 24px;
                    color: rgba(255, 255, 255, 0.8); } }
            .buttons-container {
                margin-top: 54px;
                button {
                    width: 186px;
                    height: 48px;
                    border-radius: 8px;
                    box-sizing: border-box;
                    font-size: 16px;
                    color: white;
                    &:nth-of-type(1) {
                        border: 1px solid #FFFFFF;
                        background: none; }
                    &:nth-of-type(2) {
                        background: #397DFF;
                        border: none;
                        margin-left: 24px; } } } }
        .swiper-container {
            position: absolute;
            top: 214px;
            left: 1405px;
            h1 {
                font-size: 64px;
                color: #397DFF;
                margin-bottom: 24px; }
            p {
                font-size: 14px;
                color: rgba(255, 255, 255, 0.7);
                width: 282px;
                line-height: 160%; } }
        section.popup.hidden {
            visibility: hidden; }
        section.popup {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 538px;
            height: 407px;
            background: white;
            z-index: 1;
            border-radius: 16px;
            img {
                position: absolute;
                margin: 16px 0 0 496px; }
            .popup-container {
                margin-top: 32px;
                margin-left: 42px;
                p {
                    margin: 0;
                    &:nth-of-type(1) {
                        font-size: 24px;
                        font-weight: 600; }
                    &:nth-of-type(2) {
                        margin-top: 12px;
                        font-size: 16px; } }
                form {
                    margin-top: 28px;
                    input {
                        width: 454px;
                        height: 48px;
                        border: 1px solid rgba(0, 0, 0, 0.2);
                        border-radius: 9px;
                        padding-left: 15.32px;
                        &:hover {
                            border: 1px solid #397DFF; }
                        &::placeholder {
                            color: rgba(43, 45, 58, 0.5);
                            font-size: 16px; }
                        &:nth-of-type(2) {
                            margin-top: 24px; } }
                    .input {
                        margin-top: 52px;
                        position: relative;
                        input.checkbox {
                            width: 16px;
                            height: 16px;
                            //checkbox stylisation
                            position: absolute;
                            left: -9999px; }
                        input.checkbox:checked + label:before,
                        input.checkbox:not(:checked) + label:before {
                            //checkbox stylisation
                            content: "";
                            position: absolute;
                            left: 0px;
                            top: 0px;
                            width: 16px;
                            height: 16px;
                            border: 1px solid #397DFF;
                            background-color: #EBEEF5;
                            border-radius: 2px; }
                        input.checkbox:checked + label:after,
                        input.checkbox:not(:checked) + label:after {
                            //makr inside stylisation
                            content: "";
                            position: absolute;
                            left: 3px;
                            top: 4px;
                            width: 10px;
                            height: 5px;
                            border-radius: 1px;
                            border-left: 2px solid #397DFF;
                            border-bottom: 2px solid #397DFF;
                            -webkit-transform: rotate(-45deg);
                            -moz-transform: rotate(-45deg);
                            -o-transform: rotate(-45deg);
                            -ms-transform: rotate(-45deg);
                            transform: rotate(-45deg); }
                        input.checkbox:not(:checked) + label:after {
                            opacity: 0; }
                        input.checkbox:checked + label:after {
                            opacity: 1; } }
                    label {
                        display: block;
                        font-size: 14px;
                        margin-top: -22px;
                        margin-left: 24px;
                        span {
                            color: #397DFF; } }
                    button {
                        margin-top: 16px;
                        width: 454px;
                        height: 52px;
                        background: #397DFF;
                        border-radius: 8px;
                        border: none;
                        font-size: 18px;
                        font-weight: 600;
                        color: white; } } } }
        section.popup2 {
            display: flex;
            z-index: 2;
            .popup-container {
                margin: 0;
                width: 100%;
                img {
                    margin-top: 34px;
                    margin-left: 189px; }
                p {
                    margin-top: 218px;
                    &:nth-of-type(1) {
                        font-size: 18px;
                        line-height: 162%;
                        font-weight: 500;
                        text-align: center; } }
                form {
                    margin-left: 42px;
                    button {
                        margin-top: 8px; } } } } } }
